<template>

<div class="buyOrsell" v-show="showBsell">
  <transition name="buysell">
  <div v-show="showBsell" class="content">
    <div class="news">{{$t('OrderInform')}}</div>
    <div class="contentBox">
      <span>{{$t('Name')}}</span><span class="conBox-span1">{{tickName}}</span>
    </div>
    <div  class="contentBox">
      <span>{{$t('code')}}</span><span  class="conBox-span2">{{tickPrice['instrumentid']}}</span>
    </div>
    <div  class="contentBox">
      <span>{{$t('direction')}}:</span><span v-html="tradeType" :style="{color:tradeType=='卖'?'#07B76F':'#FD4F50'}"></span>
    </div>
    <div  class="contentBox">
      <span v-if="priceType===$t('marketPrice')">{{priceType}}:</span><span v-if="priceType===$t('marketPrice')" :style="{color:diffPrice>0?'#FD4F50':(diffPrice<0?'#07B76F':'#8C8B8B')}">{{tickPrice['price']}}</span>
      <span v-if="priceType===$t('FixedPrice')">{{priceType}}:</span><span v-if="priceType===$t('FixedPrice')">{{value2}}</span>
    </div>
      <div  class="contentBox">
        <span>{{$t('Number')}}:</span><span>{{value1}}</span>
      </div>

      <div  class="contentBox">
        <span>{{$t('margin')}}:</span><span>{{mCash}}</span>
      </div>
      <div  class="contentBox">
        <span>{{$t('ServiceCharge')}}:</span><span>{{HandCash2*value1}}</span>
      </div>
<!--    <div>-->
<!--      <span>账号余额($):</span><span>{{Money}}</span>-->
<!--    </div>-->
    <div class="newFooter">
      <div class="nf-left" @click="Back"><span>{{$t('cancel')}}</span></div>
      <div class="nf-right" @click="Gochange"><span>{{$t('Confirm')}}</span></div>
    </div>
  </div>
  </transition>
</div>

</template>

<script>
  import EP from '../../utils/encryption'
export default {
  data(){
    return {
      selltype:'买',
      HandCash2:0
    }
  },
  props:{
    tradeType:{
      type:String
    },
    showBsell:{
      type:Boolean
    },
    tickPrice:{             //当前合约tick
      type:Object
    },
    tickName:{
      type:String
    },
    value1:{
      // type:Number
    },
    value2:{
      // type:Number
    },
    priceType:{
      type:String
    },
    ticknameT2:{
      type:String
    },
    HandCash:{
      // type:Number
    },
    mCash:{
      // type:Number
    },
    Money:{

    },
    Hold2:{

    },
    diffPrice:{

    }
  },
  created(){
    this.selltype = this.$route.params.type;
    this.HandCash2=this.HandCash
    console.log('this.priceType :>> ', this.priceType);
  },
  destroyed(){
    console.log('已销毁')
  },
  methods:{
    Back(){
      this.$emit('changeType',false)
    },
    Gochange(){
      this.handdleMsg();

    },
    handdleMsg() {
      let that = this;
      // console.log(that.global.ws.readyState)
      if (that.global.ws && that.global.ws.readyState === 1) {
        // console.log("发送信息");
        that.global.ws.onopen = that.onopen()
      }
      that.global.ws.onmessage = function(res) {
        if((JSON.parse(res.data))['action']==='placeorder'){
          // that.$router.go(-1);
          let msg = (JSON.parse(res.data))['data']['msg'];
          console.log(msg,'msgres')
          if(msg==='OK'){
            msg=that.$t('orderUp');
          }else if(msg === '合约不可交易'){
            msg=that.$t('orderMsg');
          }
          that.$emit('backText',msg);
        }
        if((JSON.parse(res.data))['action']==='notifitrade'){
          console.log((JSON.parse(res.data))['action'])
          that.$emit('load')
          if(that.$store.state.changeSave==='false'){
            that.$store.commit('changeSave','true')
          }else{
            that.$store.commit('changeSave','false')
          }
        }
      };
    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this;
      let direction=0;
      if(this.tradeType===this.$t('sell')){
        direction=1;
      }
      let priceType=1;
      if(this.priceType === this.$t('FixedPrice')){
        priceType=0;
      }
        actions = {
          "action":"placeorder",
          "reqID":6,
          "data":{
            "id":null,
            "userid":null,
            "settlementid":null,
            "source":null,
            "type":0,
            "positionid":null,
            "instrumentid":that.ticknameT2,
            "orderRef":"b321ecab-d67d-450c-b2bb-b8caa233877d",
            "ordersysid":null,
            "direction":direction,
            "volume":that.value1,
            "volumeTraded":null,
            "priceType":priceType,
            "limitPrice":that.value2,
            "distanceStopPrice":null,
            "distanceProfitPrice":null,
            "status":null,
            "closed":null,
            "frozenMargin":null,
            "frozenMarginMember":null,
            "tradingDay":null,
            "createtime":null,
            "updatetime":null,
            "canceltime":null,
            "hedge":null,
            "hedgeChannel":null,
            "distanceStop":0.0,
            "distanceProfit":0.0
          }};
      // console.log(JSON.stringify(actions));
      that.global.ws.send(JSON.stringify(actions));
    },
  },
  mounted(){
    let dis = EP.decrypt(localStorage.getItem('discount'),'')
    let discount = JSON.parse(dis);
    for(let obj in discount.data){
      if(obj==this.Hold2){
        console.log(discount.data[obj])
        this.HandCash2=(this.HandCash*discount.data[obj]/100)*this.value1;
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .buyOrsell {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.2);

    .content {
      width: tovw(306);
      /*height: tovw(240);*/
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%,-50%,0);
      background-color: #FFF;
      border-radius: tovw(6);
      .news {
        height: tovw(50);
        line-height: tovw(50);
        text-align: center;
        font-size: tovw(18);
        font-weight: 600;
        border-bottom: tovw(1) solid #f4f4f4;
      }
      .contentBox {
        display: flex;
        padding: tovw(10) tovw(25);

        &>span {
          flex: 1;
          display: inline-block;
        }
        &>span:nth-child(1){
          text-align: left;
          color: #666666;
        }
        &>span:nth-child(2){
          text-align: right;
          font-size: tovw(14);
          color: #222222;
        }
        .conBox-span1 {
          font-size: tovw(16);
          font-weight: 600;
        }
        .conBox-span2 {
          font-weight: 600;
        }
      }
      .newFooter {
        height: tovw(52);
        line-height: tovw(42);
        text-align: center;
        display: flex;
        .nf-left{
          color: #666666;
          position: relative;
          &>span {
            display: inline-block;
            width: tovw(103);
            height: tovw(33);
            font-size: tovw(14);
            line-height: tovw(33);
            border-radius: tovw(15);
            background-color: #EBEBEB;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateY(-70%) translateX(-50%);
          }
        }
        .nf-right{
          color: #FFF;
          position: relative;
          &>span {
            display: inline-block;
            width: tovw(103);
            height: tovw(33);
            font-size: tovw(14);
            line-height: tovw(33);
            border-radius: tovw(17);
            background-color: #2E8CFF;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateY(-70%) translateX(-50%);
          }
        }
        div {
          flex: 1;
        }
      }

    }
  }
  .buysell-enter{
    opacity: 0;
  }
  .buysell-enter-to{
    opacity: 1;
  }
  .buysell-leave-to{
    opacity: 0;
  }
  .buysell-enter-active{
    transition:all 0.4s ease
  }
  .buysell-leave-active{
    transition:all 0.3s ease
  }
</style>



