<template>
  <transition name="pay">
  <div class="pay">
    <div class="header">
      <div class="payName">
        <span class="pn-name" :class="$i18n.locale !== 'zh'?'en-pn-name':''">{{tickName}}</span>
        <span class="pn-ename">{{ticknameT}}</span>
      </div>
      <div class="newPrice"  :style="{color:diffPrice>0?$t('rise'):(diffPrice<0?$t('fall'):'')}">
        <span class="pp-in">{{Number(tickPrice.price).toFixed(digits)}}</span>
        <span class="pp-out"><span>{{diffPrice>0?'&#xe62a;':(diffPrice<0?'&#xe60e;':'')}}</span> <span>{{Math.abs(diffPrice)}}</span> <span>{{diffPriceRate}}</span></span>
      </div>
      <div class="payPrice" >
        <span class="pp-in">{{$t('Buy')}}：{{tickPrice['bidprice']}}</span>
        <span class="pp-out">{{$t('Sell')}}：{{tickPrice['askprice']}}</span>
      </div>
    </div>
     <div class="payNum">
       <span class="cp-mkprice" :class="{'changeColor':changeColor}" @click="cpColor($event)">{{$t('marketPrice')}}</span>
       <span class="cp-Liprice" :class="{'changeColor':!changeColor}" @click="cpColor($event)">{{$t('FixedPrice')}}</span>
     </div>
    <div class="changNum" >
      <span class="pn-num" :class="$i18n.locale !== 'zh'?'en-pn-num':''">{{$t('Number')}}</span>
      <div class="pn-num-con">
        <van-stepper v-model="value1" />
      </div>
    </div>
    <div class="changNum">
      <span class="pn-num" :class="$i18n.locale !== 'zh'?'en-pn-num':''">{{$t('Price')}}</span>
      <div class="pn-num-con">
        <!-- <van-stepper  v-model="value2" :decimal-length="digits" :min=0 :step="numTick"  v-if="priceType===$t('FixedPrice')" />
        <van-stepper v-model="value2" :decimal-length="digits" :min=0 disabled v-if="priceType===$t('marketPrice')" />--> -->
        <!-- --------------------------zlq--------修改市价的价格不会随着当前价格的改动而改动--------------------------- -->
        <van-stepper  v-model="value2" :decimal-length="digits" :min=0 :step="numTick"  v-if="priceType===$t('FixedPrice')" />
        <van-stepper v-model="tickPrice.price" :decimal-length="digits" :min=0 disabled v-if="priceType===$t('marketPrice')" /> -->
        <!-- --------------------------zlq----------------------------------- -->
      </div>
    </div>
<!--     <div class="choosePrice">-->
<!--       <span class="cp-price">价格:</span>-->
<!--       <div class="cp-Num"  v-if="priceType==='限价'">-->
<!--         <van-stepper  v-model="value2" />-->
<!--       </div>-->
<!--       <div class="cp-Num"  v-if="priceType==='市价'">-->
<!--         <van-stepper v-model="value2" disabled />-->
<!--       </div>-->
<!--     </div>-->
<!--     <div class="promisePrice">-->
<!--       <span>保证金：{{marginCash}}</span>-->
<!--     </div>-->
     <div class="payOut">
       <span class="po-out" :style="{backgroundColor:$t('fall')}" @click="buysell($event)">{{$t('sell')}}</span>
       <span class="po-in" :style="{backgroundColor:$t('rise')}" @click="buysell($event)">{{$t('buy')}}</span>
     </div>
    <span class="black" @click="back"></span>
<!--    <router-view></router-view>-->
    <!-- <buysell :diffPrice="diffPrice" @backText="backText"  @load="Load" :tradeType ="tradeType" :Hold2="hold2" :showBsell="showBsell" @changeType = 'changeType' :tickPrice="tickPrice" :tickName="tickName" :value1="value1" :value2="value2" :priceType="priceType" :ticknameT2="ticknameT" :HandCash="openHandCash" :mCash="marginCash" :Money="PersonMoney"></buysell> -->
    <!-- --------------------------------zlq-------------------------------------- -->
    <buysell :diffPrice="diffPrice" @backText="backText"  @load="Load" :tradeType ="tradeType" :Hold2="hold2" :showBsell="showBsell" @changeType = 'changeType' :tickPrice="tickPrice" :tickName="tickName" :value1="value1" :value2="value2" :priceType="priceType" :ticknameT2="ticknameT" :HandCash="openHandCash" :mCash="marginCash" :Money="PersonMoney"></buysell>
    <!-- --------------------------------zlq-------------------------------------- -->
  </div>
  </transition>
</template>

<script>
  import Vue from 'vue';
  import { Stepper } from 'vant';
  import buysell from './buyOrsell'
  Vue.use(Stepper);
  export default {
    data(){
      return {
        value1:1,
        value2:0,
        changeColor:true,
        // showinout:true
        showBsell:false,
        tradeType:'',
        priceType:this.$t('marketPrice'),
        marginCash:0,
        openHandCash:0,
        PersonMoney:'获取中...',
        hold2:'',
        diffPrice:0,
        diffPriceRate:0
      }
    },
    props:{
      showP:{
      default:true
      },
      tickPrice:{           //当前合约tick
        type:Object
      },
      digits:{

      },
      tickName:{        //合约名称汉字
        type:String
      },
      ticknameT:{        //合约名称英文
        type:String
      },
      openHandM:{
        type:Number
      },
      margin:{
        type:Number
      },
      holdPositions:{

      },
      name:{

      },
      item2:{

      },
      numTick:{

      }
    },
    methods:{
      Load(){
        this.$emit('reload')
      },
      backText(res){
        this.$emit('backText',res);
        this.showBsell=false;
      },
      back(){
        let path = this.$route.path;
        path = path.split('/');
        path=path[path.length-1];
        // alert(path)
        if(path!=='selfchoose'){
          this.$router.go(-1)
        }else {
          this.$emit('back')
        }

      },
      reduce(e){
        if(this.value<2){
         // console.log(e.target.setAttribute('color','#FFF'));
          e.target.setAttribute('color','#FFF');
          return
        }
        this.value--;
      },
      add(){
        this.value++;
      },
      cpColor(e){
        // console.log(this.priceType === e.target.innerText)
        if(this.priceType === e.target.innerText){
          return
        }
        this.priceType = e.target.innerText;
        this.changeColor=!this.changeColor;
        // console.log(this.priceType)
      },
      buysell(e){
        this.tradeType = e.target.innerText;
        this.showBsell = true;
        // console.log(this.marginCash)
      },
      changeType(req){
        this.showBsell = req
      },
      request(){
        let this_=this;
        this.global.ws.onopen = this.onopen();
        this.global.ws.onmessage=function (res) {
          console.log(res)
          if(JSON.parse(res.data).action==='loadusermoney'){
            console.log(JSON.parse(res.data).data.object['balance'])
            this_.PersonMoney = JSON.parse(res.data).data.object['balance'];
          }
        }
      },
      onopen () { // websocket连接后发送数据(send发送)
        let actions;
        actions = {
          action: "loadusermoney",
          reqID:12,
          data: {
            userId:localStorage.getItem('uid')
          },
        };
        this.global.ws.send(JSON.stringify(actions));
        // console.log(JSON.stringify(actions));
      }
    },
    components:{
      buysell
    },
    created(){
      this.request();
      this.openHandCash = this.openHandM;
      this.marginCash = this.margin;
      this.value2=this.tickPrice['price'];
      console.log(this.tickPrice)
      for(let i=0;i<this.holdPositions.length;i++){
        if(this.holdPositions[i].instrumentid===this.ticknameT){
          this.hold2=this.holdPositions[i].groupid;
          console.log(this.hold2)
        }
      }
    },
    mounted(){
      this.diffPrice=(Number(this.tickPrice['price'])-Number(this.tickPrice['preclose'])).toFixed(this.digits);
      this.diffPriceRate=(Math.abs(this.diffPrice)/Number(this.tickPrice['preclose'])*100).toFixed(3)+'%'
      console.log(this.tickPrice)
      console.log(Number(this.tickPrice['preclose']))
    },
    watch:{
      value1(newVal){
        this.openHandCash = Number(newVal)*this.openHandM;
        this.marginCash = Number(newVal)*this.margin;
      },
      tickPrice(newVal){
        this.diffPrice=(Number(newVal['price'])-Number(newVal['preclose'])).toFixed(this.digits);
        this.diffPriceRate=(Math.abs(this.diffPrice)/Number(newVal['preclose'])*100).toFixed(2)+'%'
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../style/theme2";
.pay {
  width: 100vw;
  height: tovw(260);
  background-color: #FFF;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 8;
  font-size: tovw(15);
  padding: tovw(12) tovw(20) tovw(0) tovw(20);
  border-radius: tovw(15) tovw(15) 0 0;
  .header {
    width: 100%;
    height: tovw(60);
    font-size: 0;
    .payName {
      display: inline-block;
      width: tovw(80);
      .en-pn-name{
        line-height: tovw(24)!important;
        font-size: tovw(12)!important;
      }
      &>span {
        display: block;
        font-size: tovw(14);
      }
      &>span:nth-child(1){
        font-size: tovw(18);
      }
    }
    .newPrice {
      display: inline-block;
      width: tovw(124);
      text-align: center;
      font-size: tovw(14);
      font-family:'iconfont' !important;
      &.changeRed {
        color:#FD4F50;
      }
      &.changeGre {
        color:#07B76F;
      }
      &>span {
        display: block;
      }
      &>span:nth-child(1){
        font-size: tovw(18);
      }
      .pp-out {
        &>span:nth-child(1) {
          display:inline-block;
          font-size: tovw(8);
          line-height:tovw(18);
          vertical-align: top;
        }
        &>span:nth-child(2) {
          display:inline-block;
          font-size: tovw(14);
          line-height:tovw(18);
          vertical-align: top;
        }
        &>span:nth-child(3) {
          display:inline-block;
          font-size: tovw(14);
          line-height:tovw(18);
          vertical-align: top;
        }
      }
    }
    .payPrice {
      display: inline-block;
      width: tovw(124);
      font-size: tovw(12);
      line-height: tovw(22);
      text-align: right;
      vertical-align: top;
      .pp-in {
        color: #FE0000;
        display: block;
      }
      .pp-out {
        color: #25B100;
        display: block;
        &>span:nth-child(1) {
          margin-right: tovw(2);
        }
      }
    }
  }
  .payNum {
    font-size: tovw(16);
    &>span {
      display: inline-block;
      padding: tovw(4) tovw(7) tovw(5) tovw(7);
      font-size: tovw(16);
      &.changeColor {
        background-color: rgba(25,136,225,0.08);
        color:#1988FF;
      }
    }
  }
  .changNum {
    height: tovw(30);
    font-size: 0;
    width: 100%;
    margin-top: tovw(15);
    display: flex;
    .en-pn-num{
      font-size: tovw(12)!important;
    }
    .pn-num {
      display: inline-block;
      font-size: tovw(18);
      line-height: tovw(30);
      height: tovw(30);
      padding-right: tovw(4);
      vertical-align: top;
      /*margin-right: tovw(5);*/
    }
    .pn-num-con {
      display: inline-block;
      margin-left: auto;
    }
    }
  .payOut {
    text-align: center;
    width: 100%;
    height: tovw(33);
    .po-in,.po-out {
      display: inline-block;
      width: tovw(110);
      height: tovw(33);
      border-radius: tovw(17);
      color: #FFF;
      line-height: tovw(33);
      text-align: center;
      margin-top: tovw(16);
    }
    .po-in {
      background-color: #FE0008;
    }
    .po-out {
      background-color: #25B102;
      margin-right: tovw(77);
    }
  }
}
</style>



